						<span>{{ item.text }}</span>
					</router-link>
				</div>
			</div>
			<div class="table_content_body_right">
				<router-view></router-view>
			</div>
			<div class="clear"> </div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'approvalList',
		data(){
			return {
				leftNav: [
					{
						component: "fundDistribution",
						icon: 'guanligongsigaikuang',
						text: '基金分配'
					},
					{
						component: "fundPayment",
						icon: 'zaiguanjijin',
						text: '基金缴款'
					},
					{
						component: "fundReturn",
						icon: 'zaiguanjijin',
						text: '基金回款'
					}
				]
			}
		}
	}
</script>

